@use "variables";

@mixin base-btn {
    cursor: pointer;
    height: 42px;
    padding: 0 25px;
    font-size: 1em;
    font-weight: 500;
    border: 1px solid transparent;
    transition: all 0.5s ease-in-out;
    background-color: variables.$teal;
    color: white;
    // border-radius: 7px;

    &:active,
    &:focus {
        outline: none;
    }

    &:hover {
        background-color: variables.$dark-teal;
    }
}

@mixin input-element {
    input {
        font-family: "Roboto", sans-serif;
        color: variables.$charcoal;
        padding: 5px 0px;
        width: 100%;
        border: 0px;
        border-bottom: 1px solid variables.$teal;
        background-color: transparent;
        transition: 0.5s all ease-in-out;
        font-size: 1.1em;
        margin-bottom: 21px;

        &:focus {
            outline: none;
            border-bottom: 1px solid variables.$dark-teal;
        }
    }
}

@mixin base-grid {
    display: grid;
    gap: 21px;
}