@use "mixins";
@use "variables";

.portfolio-form-wrapper {
  @include mixins.base-grid();
  grid-template-columns: 1fr;
  padding: 42px;

  @include mixins.input-element();

  input {
    margin-bottom: 0;
  }

  .image-uploaders {
    @include mixins.base-grid();
    grid-template-columns: 200px 200px 200px;

    .portfolio-manager-image-wrapper {
      img {
        width: 100%;
        max-width: 120px;
        height: 120px;
        object-fit: cover;
        border-radius: 5px;
      }

      .image-removal-link {
        display: flex;
        justify-content: center;
        a {
          font-weight: 900;
          color: variables.$warning;
          cursor: pointer;
        }
      }
    }
  }
}
